<template>
  <v-container>
    <v-row>
      <v-stepper v-model="e1">
        <v-stepper-header>
          <v-stepper-step
            step="1"
            @click="e1 = 1"
          >Пользователь</v-stepper-step>

          <v-divider></v-divider>

          <v-stepper-step
            step="2"
            @click="e1 = 2"
          >Клиент</v-stepper-step>
        </v-stepper-header>
        <v-stepper-content
          step="1"
        >
          <v-card-text>
            <v-container>
              <v-row>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  При создании бронирования подтверждение отправить клиенту на
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    dense
                    v-model="notifyOnBooking"
                    :items="notifiesTo"
                  />
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  При создании резерва отправить на
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    dense
                    v-model="notifyOnReserve"
                    :items="notifiesTo"
                  />
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  При получении брони от источников
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-checkbox
                    v-model="onGetBooking"
                    dense
                    color="green"
                  />
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  При изменении статуса брони на
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    v-model="vBookingStatus"
                    dense
                    :items="vBookingStatuses"
                    chips
                    multiple
                  />
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  При изменении статуса уборки номера на
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    v-model="cleaningStatus"
                    dense
                    :items="cleaningStatuses"
                  />
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  Отправлять уведомление
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    v-model="sendNotify"
                    dense
                    :items="sendNotifies"
                  />
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  Уведомление о брони (резерв,бронирование,отмена)
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    v-model="notifyOnBookings"
                    dense
                    :items="notifiesTo"
                  />
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  При изменении статуса брони на
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    v-model="statusTo"
                    dense
                    :items="statusesTo"
                  />
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  При изменении статуса уборки номера на
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    v-model="cleaningStatusChange"
                    dense
                    :items="cleaningStatusesChange"
                  />
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  Уведомления об Ошибках
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    v-model="vBookingStatus"
                    :items="vBookingStatuses"
                    dense
                  />
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
        </v-stepper-content>
        <v-stepper-content
          step="2"
        >
          <v-card-text>
            <v-container>
              <v-row>
                <v-col
                  class="pb-0 pt-0"
                  cols="5"
                >
                  Типы писем для отправки клиентам
                </v-col>
                <v-col
                  class="pb-0 pt-0"
                  cols="7"
                >
                  <v-select
                    dense
                    v-model="letterType"
                    :items="lettersType"
                  />
                </v-col>
              </v-row>
            </v-container>
          </v-card-text>
        </v-stepper-content>
      </v-stepper>
    </v-row>
  </v-container>
</template>

<script>
  export default {
    name: 'Notifications',
    data: () => ({
      e1: 1,
      notifyOnBooking: '',
      notifyOnReserve: '',
      notifyOnBookings: '',
      onGetBooking: false,
      notifiesTo: [
        { text: 'почту', value: 'email' },
        { text: 'телеграм', value: 'telegram' },
        { text: 'ватсап ', value: 'whatsapp' },
      ],
      vBookingStatus: '',
      vBookingStatuses: [
        { text: 'Статус-1', value: 'accepted' },
        { text: 'Статус-2', value: 'other' },
        { text: 'Статус-3', value: 'whatsapp' },
      ],
      cleaningStatus: '',
      cleaningStatuses: [
        { text: 'чистый', value: 'clear' },
        { text: 'уборка', value: 'cleaning' },
        { text: 'грязный', value: 'dirty' },
      ],
      sendNotify: '',
      sendNotifies: [
        { text: 'уведомление-1', value: '3' },
        { text: 'уведомление-2', value: '2' },
        { text: 'уведомление-3', value: '1' },
      ],
      statusTo: '',
      statusesTo: [
        { text: 'Подтверждено', value: '3' },
        { text: 'не подтверждено', value: '2' },
        { text: 'отменено календарь', value: '1' },
        { text: 'почта', value: '4' },
        { text: 'телеграм ', value: '5' },
      ],
      cleaningStatusChange: '',
      cleaningStatusesChange: [
        { text: 'чистый', value: 'clear' },
        { text: 'уборка', value: 'cleaning' },
        { text: 'грязный', value: 'dirty' },
      ],
      letterType: '',
      lettersType: [
        { text: 'Резерв', value: 'reserve' },
        { text: 'Бронь', value: 'booking' },
        { text: 'Отмена брони ', value: 'bookingCancel' },
      ],
    }),
  }
</script>

